import React, { useState } from "react";
import "./../KittyCalculator.css";

const KittyCalculator = () => {
  const [value, setValue] = useState("");

  return (
    <div className="KCalculatorWrapper">
      <div className="KCalculator">
        <img
          src={"./images/download.png"}
          alt="hello kitty"
          className="CalcSticker"
        />
        <form>
          <div className="Kdisplay">
            <input type="text" value={value} readOnly />
          </div>

          <div className="buttonRow">
            <input type="button" value="AC" onClick={() => setValue("")} />
            <input
              type="button"
              value="DE"
              onClick={() => setValue(value.slice(0, -1))}
            />
            <input
              type="button"
              value="."
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="/"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="x"
              onClick={() => setValue(Math.pow(Number(value), 2))}
            />
          </div>

          <div className="buttonRow">
            <input
              type="button"
              value="7"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="8"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="9"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="*"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="x"
              onClick={() => setValue(Math.pow(Number(value), 3))}
            />
          </div>

          <div className="buttonRow">
            <input
              type="button"
              value="4"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="5"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="6"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="+"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value=""
              onClick={() => setValue(Math.sqrt(Number(value)))}
            />
          </div>

          <div className="buttonRow">
            <input
              type="button"
              value="1"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="2"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="3"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="-"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="E"
              onClick={() => setValue(value + "e")}
            />
          </div>

          <div className="buttonRow">
            <input
              type="button"
              value="0"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="00"
              onClick={(e) => setValue(value + e.target.value)}
            />
            <input
              type="button"
              value="="
              className="SignEqual"
              onClick={() => {
                try {
                  const result = eval(value);
                  setValue(result.toString());
                } catch {
                  setValue("Error");
                }
              }}
            />
          </div>
        </form>
      </div>
    </div>
  );
};

export default KittyCalculator;
