import React from "react";
import "./../FormulaBook.css";
import HTMLFlipBook from "react-pageflip";

const FormulaBook = () => {
  return (
    <div className="formula-book-wrapper">
      <HTMLFlipBook
        width={450}
        height={400}
        maxShadowOpacity={0.5}
        drawShadow={true}
        showCover={true}
        size="fixed"
        mobileScrollSupport={true}
        useMouseEvents={true}
        swipeDistance={30}
        className="formula-flipbook"
      >
        <div className="page front-cover">
          <div className="page-content">
            <img
              src={"./images/download1.png"}
              alt="KittyImg"
              className="Kitty-logo"
            />
            <h1>A-Level Math</h1>
          </div>
        </div>

        {/* Page 1: Quadratic & Binomial */}
        <div className="page">
          <div className="page-content">
            <h1>Algebra</h1>
            <div className="formula-list">
              <h1>Algebra</h1>
              <h3>Quadratic Formula</h3>
              <p className="formula">x = [-b  (b - 4ac)] / 2a</p>

              <h3>Binomial Expansion</h3>
              <p className="formula">(a + b) = a + 2ab + b</p>
              <p className="formula">(a + b) = a + 3ab + 3ab + b</p>
              <p className="formula">
                (a + b) = a + 4ab + 6ab + 4ab + b
              </p>
              <p className="formula">(1 + x) = 1 + nx + n(n-1)x/2! + ...</p>
            </div>
          </div>
        </div>

        {/* Page 2: Arithmetic Sequences */}
        <div className="page">
          <div className="page-content">
            <h1>Arithmetic Sequences</h1>
            <div className="formula-list">
              <h3>nth term</h3>
              <p className="formula">a = a + (n-1)d</p>

              <h3>Sum of first n terms</h3>
              <p className="formula">S = n/2 [2a + (n-1)d]</p>
              <p className="formula">S = n/2 (a + l)</p>

              <h3>Common difference</h3>
              <p>d = a - a</p>
            </div>
          </div>
        </div>

        {/* Page 3: Geometric Sequences */}
        <div className="page">
          <div className="page-content">
            <h1>Geometric Sequences</h1>
            <div className="formula-list">
              <h3>nth term</h3>
              <p className="formula">a = ar</p>

              <h3>Sum of first n terms</h3>
              <p className="formula">S = a(1 - r)/(1 - r)</p>

              <h3>Sum to infinity</h3>
              <p className="formula">S_ = a/(1 - r)</p>
            </div>
          </div>
        </div>

        {/* Page 4: Statistics Distributions */}
        <div className="page">
          <div className="page-content">
            <h1>Statistics Distributions</h1>
            <div className="formula-list">
              <h1>Statistics Distributions</h1>
              <h3>Binomial Distribution</h3>
              <p>X ~ B(n, p)</p>
              <p className="formula">P(X=r) = C p (1-p)</p>
              <p>Mean:  = np</p>
              <p>Variance:  = np(1-p)</p>

              <h3>Normal Distribution</h3>
              <p>X ~ N(, )</p>
              <p className="formula">z = (x - )/</p>
              <p>Standard Normal: Z ~ N(0, 1)</p>
            </div>
          </div>
        </div>

        {/* Back Cover */}
        <div className="page back-cover">
          <div className="page-content">
            <h1>END</h1>
          </div>
        </div>
      </HTMLFlipBook>
    </div>
  );
};

export default FormulaBook;
